                                  class="ml-2 mr-2"></v-select>
                          <p class="pt-4">топқа бөлу</p>
                        </div>
                      </v-radio>
                    </v-radio-group>
                  </v-flex>
                </v-layout>
              </v-card-title>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-dialog v-model="dialog" persistent scrollable max-width="700px">
                  <v-btn
                          slot="activator"
                          class="primary mr-2 mb-2"
                          @click="randomize()">Randomize!</v-btn>
                  <v-card>
                    <v-card-title class="headline">
                      <div v-if="radio === 'generateList'">Кездейсоқ тізім</div>
                      <div v-else>Кездейсоқ {{ selectedGroupsCount }} топ</div>
                    </v-card-title>
                    <v-card-text>
                      <div v-if="radio === 'generateList'">
                        <p v-for="(student, index) in studentsss" :key="index">
                          {{ index + 1 }}) {{ student }}
                        </p>
                      </div>
                      <div v-else>
                        <div v-for="(item, index) in generatedGroups" :key="index">
                          <strong>{{ index + 1 }} Топ:</strong>
                          <span v-for="miniItem in item" :key="miniItem">
                            {{ miniItem }}
                          </span>
                        </div>
                      </div>
                    </v-card-text>
                    <v-card-actions>
                      <v-spacer></v-spacer>
