<script>
Array.prototype.contains = function(obj) {
    var i = this.length;
    while (i--) {
        if (this[i] == obj) {
            return true;
        }
    }
    return false;
}

function shuffle(a) {
    let j, x, i;
    for (i = a.length - 1; i > 0; i--) {
        j = Math.floor(Math.random() * (i + 1));
        x = a[i];
        a[i] = a[j];
        a[j] = x;
    }
    return a
}

export default {
  name: 'App',
  data: () => ({
      dark: false,
      radio: "generateList",
      dialog: false,
      group: "Сынып",
      selectedStudents: [],
      selectedGroupsCount: 2,
      groups: [
          "Сынып", "1-ші топ", "2-ші топ"
      ],
      groupsCountt: [
          2, 3 ,4, 6
      ],
      students: ["Жанибек", "Толкынай", "Алих-Семск", "Дамиля", "Анель", "Данай",
          "Арай", "Ернар", "Ажар", "Айнур", "Сайфуддин", "Алан", "Олжас",
          "Елигай", "Маулен", "Амина", "Нуржигит", "Алихан", "Зере", "Мухсин",
          "Тогжан", "Сырым", "Акежан", "Дарын"].sort(),
      girlss: ["Толкынай", "Дамиля", "Анель", "Арай", "Ажар",
          "Айнур", "Елигай", "Амина", "Зере", "Тогжан"],
      secondGroupStudents: ["Жанибек", "Дамиля", "Анель", "Арай", "Ернар",
          "Алан", "Олжас", "Маулен", "Нуржигит", "Алихан", "Акежан", "Дарын"].sort(),
      studentsss: [],
      generatedGroups: null
  }),
  methods: {
      changeTheme() {
          this.dark = !this.dark
      },
      randomize() {
          let rnd = shuffle(this.studentss)
          let sz = rnd.length
          if(this.radio === "generateList")
              this.studentsss = rnd
          else {
              // let tmp = Math.floor(sz / this.selectedGroupsCount)
              // let r = sz, arr = [], it = 0, cnt = this.selectedGroupsCount
              // for(let i = 0; i < this.selectedGroupsCount; i++) {
              //     arr.push([])
              // }
              // for(let i = 0; i < this.selectedGroupsCount; i++) {
              //     if(r % cnt-- !== 0) {
              //         r -= (tmp + 1)
              //         for (let l = 1; l <= tmp + 1; l++) {
              //             arr[i].push(rnd[it++])
              //         }
              //     }
              //     else {
              //         r -= tmp
              //         for(let l = 1; l <= tmp; l++) {
              //             arr[i].push(rnd[it++])
              //         }
              //     }
              // }
              // this.generatedGroups = [...arr]
              let tmp = Math.floor(sz / this.selectedGroupsCount)
              let cntTmp2 = sz - (tmp * this.selectedGroupsCount)
              let cntTmp = this.selectedGroupsCount - cntTmp2
              let tmp2 = tmp + 1
              let arr = []
              let rndBoys = shuffle(this.boys)

              // let rndBoys = shuffle(this.boys)
              // let tmpRndBoys = Math.floor(rndBoys.length / this.selectedGroupsCount)
              // let cntTmp2RndBoys = rndBoys.length - (tmp * this.selectedGroupsCount)
              // let cntTmpRndBoys = this.selectedGroupsCount - cntTmp2RndBoys
              // let tmp2RndBoys = tmpRndBoys + 1

              let rndGirls = shuffle(this.girls)
              let tmpRndGirls = Math.floor(rndGirls.length / this.selectedGroupsCount) //min count of girls
              let cntTmp2RndGirls = (rndGirls.length - (tmpRndGirls * this.selectedGroupsCount)) //count of groups
              let cntTmpRndGirls = this.selectedGroupsCount - cntTmp2RndGirls
              let tmp2RndGirls = tmpRndGirls + 1
              let it = 0, it2 = 0


              for(let i = 0; i < cntTmp; i++) {
                  //list of 'tmp' (count) students
                  let arrr = []
                  for(let j = 0; j < tmpRndGirls; j++) {
                      arrr.push(rndGirls[it++])
                  }
                  for(let j = 0; j < tmp - tmpRndGirls; j++) {
                      arrr.push(rndBoys[it2++])
                  }
                  cntTmpRndGirls--
                  if(cntTmpRndGirls <= 0) {
                      cntTmpRndGirls = cntTmp2RndGirls
                      tmpRndGirls = tmp2RndGirls
                  }
                  arr.push(arrr)
              }

              for(let i = 0; i < cntTmp2; i++) {
                  let arrr = []
                  for(let j = 0; j < tmpRndGirls; j++) {
                      arrr.push(rndGirls[it++])
                  }
                  for(let j = 0; j < tmp2 - tmpRndGirls; j++) {
                      arrr.push(rndBoys[it2++])
                  }
                  cntTmpRndGirls--
                  if(cntTmpRndGirls <= 0) {
                      cntTmpRndGirls = cntTmp2RndGirls
                      tmpRndGirls = tmp2RndGirls
                  }
                  arr.push(arrr)
              }
              this.generatedGroups = [...arr]
          }
      }
  },
  computed: {
      boyss() {
          let b = []
          for(let i = 0; i < 24; i++) {
              if(!this.girls.contains(this.students[i])) {
                  b.push(this.students[i])
              }
          }
          return b
      },
      groupsCount() {
          if(this.group === "Сынып")
              return [...this.groupsCountt, 12]
          else
              return this.groupsCountt
      },
      firstGroupStudents() {
          let arr = [...this.students]
          for(let i = 0; i < this.secondGroupStudents.length; i++) {
              let index = arr.indexOf(this.secondGroupStudents[i])
              arr.splice(index, 1)
          }
          return arr
      },
      studentss() {
          if(this.group === "Сынып") {
              let arr = [...this.students]
              for(let i = 0; i < this.selectedStudents.length; i++) {
                  let index = arr.indexOf(this.selectedStudents[i])
                  arr.splice(index, 1)
              }
              return arr
          }
          if(this.group === "1-ші топ") {
              let arr = [...this.firstGroupStudents]
              for(let i = 0; i < this.selectedStudents.length; i++) {
                  let index = arr.indexOf(this.selectedStudents[i])
                  arr.splice(index, 1)
              }
              return arr
          }
          let arr = [...this.secondGroupStudents]
          for(let i = 0; i < this.selectedStudents.length; i++) {
              let index = arr.indexOf(this.selectedStudents[i])
              arr.splice(index, 1)
          }
          return arr
      },
      studentssss() {
          if(this.group === "Сынып")
              return this.students
          if(this.group === "1-ші топ")
              return this.firstGroupStudents
          return this.secondGroupStudents
      },
      girls() {
          let arr = [...this.girlss]
          for(let i = 0; i < this.selectedStudents.length; i++) {
              if(arr.contains(this.selectedStudents[i]))
                  arr.splice(arr.indexOf(this.selectedStudents[i]), 1)
          }
          if(this.group === "1-ші топ") {
              for(let i = 0; i < this.secondGroupStudents.length; i++) {
                  if(arr.contains(this.secondGroupStudents[i]))
                      arr.splice(arr.indexOf(this.secondGroupStudents[i]), 1)
              }
          }
          else if(this.group === "2-ші топ") {
              for(let i = 0; i < this.firstGroupStudents.length; i++) {
                  if(arr.contains(this.firstGroupStudents[i]))
                      arr.splice(arr.indexOf(this.firstGroupStudents[i]), 1)
              }
          }
          return arr
          // if(this.group === "Сынып") {
          //     let arr = [...this.students]
          //     for(let i = 0; i < this.selectedStudents.length; i++) {
          //         let index = arr.indexOf(this.selectedStudents[i])
          //         arr.splice(index, 1)
          //     }
          //     return arr
          // }
          // if(this.group === "1-ші топ") {
          //     let arr = [...this.firstGroupStudents]
          //     for(let i = 0; i < this.selectedStudents.length; i++) {
          //         let index = arr.indexOf(this.selectedStudents[i])
          //         arr.splice(index, 1)
          //     }
          //     for(let i = 0; i < this.girlss.length; i++) {
          //         if(arr.contains(this.girlss[i])) {
          //             //
          //         }
          //     }
          //     return arr
          // }
          // let arr = [...this.secondGroupStudents]
          // for(let i = 0; i < this.selectedStudents.length; i++) {
          //     let index = arr.indexOf(this.selectedStudents[i])
          //     arr.splice(index, 1)
          // }
          // return arr
      },
      boys() {
          let arr = [...this.boyss]
          for(let i = 0; i < this.selectedStudents.length; i++) {
              if(arr.contains(this.selectedStudents[i]))
                  arr.splice(arr.indexOf(this.selectedStudents[i]), 1)
          }
          if(this.group === "1-ші топ") {
              for(let i = 0; i < this.secondGroupStudents.length; i++) {
                  if(arr.contains(this.secondGroupStudents[i]))
                      arr.splice(arr.indexOf(this.secondGroupStudents[i]), 1)
              }
          }
          else if(this.group === "2-ші топ") {
              for(let i = 0; i < this.firstGroupStudents.length; i++) {
                  if(arr.contains(this.firstGroupStudents[i]))
                      arr.splice(arr.indexOf(this.firstGroupStudents[i]), 1)
              }
          }
          return arr
      }
  }
}
</script>

